import React from 'react';
import FiveDayCard from './Cards/fiveDayCard';
import Form from './CurrentWeatherComponents/form';
//import { WiDaySunny, WiRain, WiCloudy, WiDayWindy, WiSnow, WiThunderstorm, WiHail } from 'weather-icons-react';

const API_KEY = "682fb94b532d981c22c94d9c494437aa"

export default class FiveDayForcast extends React.Component {
	state = {
		temperature: [],
		time: [],
		city: undefined,
		country: undefined,
		description: [],
		error: undefined
	}
	
	getWeather = async (e) => {
		e.preventDefault();
		const city = e.target.elements.city.value;
		const country = e.target.elements.country.value;
		const api_call = await fetch(`https://api.openweathermap.org/data/2.5/forecast?q=${city},${country}ca&appid=${API_KEY}&units=metric`);
		const data = await api_call.json();
		if (city && country) {
			console.log(data);
			this.setState ({
				temperature: data.list.map( temp => temp.main.temp),
				time: data.list.map( time => time.dt_txt),
				city: data.city.name,
				country: data.city.country,
				description: data.list.map( description => description.weather[0].description),
				error: ""
			});
			console.log(this.state.time);
		} else {
			this.setState({
				temperature: undefined,
				time: undefined,
				city: undefined,
				country: undefined,
				description: undefined,
				error: "Please enter a value."	
			});
		}
	}

	render() {
		const weatherAppContainer = {
			display: "flex",
			flexWrap: "wrap",
			width: "700px",
			height: "160px",
		}
		
		const boldForm = {
			fontWeight: 'bold',
			color: 'black',
		}
		
		return (
		<div style={boldForm}>
			<Form getWeather={this.getWeather}/>
			<div style={weatherAppContainer}>
				<FiveDayCard 
					temperature={this.state.temperature[0]}
					time={this.state.time[0]}
					city={this.state.city}
					country={this.state.country}
					description={this.state.description[0]}
					error={this.state.error} 
				/>
				<FiveDayCard 
					temperature={this.state.temperature[4]}
					time={this.state.time[4]}
					city={this.state.city}
					country={this.state.country}
					description={this.state.description[4]}
					error={this.state.error} 
				/>
				<FiveDayCard 
					temperature={this.state.temperature[8]}
					time={this.state.time[8]}
					city={this.state.city}
					country={this.state.country}
					description={this.state.description[8]}
					error={this.state.error} 
				/>
				<FiveDayCard 
					temperature={this.state.temperature[12]}
					time={this.state.time[12]}
					city={this.state.city}
					country={this.state.country}
					description={this.state.description[12]}
					error={this.state.error} 
				/>
				<FiveDayCard 
					temperature={this.state.temperature[16]}
					time={this.state.time[16]}
					city={this.state.city}
					country={this.state.country}
					description={this.state.description[16]}
					error={this.state.error} 
				/>
				<FiveDayCard 
					temperature={this.state.temperature[20]}
					time={this.state.time[20]}
					city={this.state.city}
					country={this.state.country}
					description={this.state.description[20]}
					error={this.state.error} 
				/>
				<FiveDayCard 
					temperature={this.state.temperature[24]}
					time={this.state.time[24]}
					city={this.state.city}
					country={this.state.country}
					description={this.state.description[24]}
					error={this.state.error} 
				/>
				<FiveDayCard 
					temperature={this.state.temperature[28]}
					time={this.state.time[28]}
					city={this.state.city}
					country={this.state.country}
					description={this.state.description[28]}
					error={this.state.error} 
				/>
				<FiveDayCard 
					temperature={this.state.temperature[32]}
					time={this.state.time[32]}
					city={this.state.city}
					country={this.state.country}
					description={this.state.description[32]}
					error={this.state.error} 
				/>
				<FiveDayCard 
					temperature={this.state.temperature[36]}
					time={this.state.time[36]}
					city={this.state.city}
					country={this.state.country}
					description={this.state.description[36]}
					error={this.state.error} 
				/>
			</div>
		</div>
		);
	}
}