import React from 'react';

const cardContainer = {
	width: "126px",
	height: "150px",
	textAlign: "center",
	display: "flex",
	flexDirection: "column",
	border: "solid",
	borderRadius: "7px"
}
		
/*const weatherIcon = {
	align: "center"
}*/

const FiveDayCard = props => (
<div>	
	{props.temperature && <div style={cardContainer}> 
		{ props.city && props.country && <p>{props.city}, {props.country}</p> }
		{ props.time && <p>time: {props.time}</p> }
		{ props.temperature && <p>{props.temperature}Â°</p> }
		{ props.description && <p>{props.description}</p> }
		{ props.error && <p> {props.error} </p> }
	</div> }
</div>
);
	
export default FiveDayCard;