import React from 'react';
import Form from './CurrentWeatherComponents/form';
import Weather from './CurrentWeatherComponents/weather';

const API_KEY = "682fb94b532d981c22c94d9c494437aa"

export default class CurrentWeather extends React.Component {
	state = {
		temperature: undefined,
		city: undefined,
		country: undefined,
		humidity: undefined,
		description: undefined,
		error: undefined
	}
	
	getWeather = async (e) => {
		e.preventDefault();
		const city = e.target.elements.city.value;
		const country = e.target.elements.country.value;
		const api_call = await fetch(`https://api.openweathermap.org/data/2.5/weather?q=${city},${country}ca&appid=${API_KEY}&units=metric`);
		const data = await api_call.json();
		if (city && country) {
			console.log(data);
			this.setState({
				temperature: data.main.temp,
				city: data.name,
				country: data.sys.country,
				humidity: data.main.humidity,
				description: data.weather[0].description,
				error: ""
			});
		} else {
			this.setState({
				temperature: undefined,
				city: undefined,
				country: undefined,
				humidity: undefined,
				description: undefined,
				error: "Please enter a value."	
			});
		}
	}

  render(){	
		const currentWeatherContainer = {
		fontWeight: 'bold',
		color: 'black',
	}
		
    return (               
				<div style={currentWeatherContainer}>
					<Form getWeather={this.getWeather}/>
					<Weather 
						temperature={this.state.temperature}
						city={this.state.city}
						country={this.state.country}
						humidity={this.state.humidity}
						description={this.state.description}
						error={this.state.error} 
					/>
				</div>
			);
	}
}